
.footer__wrapper {
  padding: 39px 20px 22px;

  @media (min-width: $tablet-width) {
    padding: 54px 30px;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }

  @media (min-width: $desktop-width) {
    padding: 57px 110px;
  }
}

.footer__list {
  @include clear-list;
  display: flex;
  justify-content: center;
  align-items: center;
  column-gap: 35px;
  padding: 18px 0;
  border-top: 1px solid $bg-grey;
  border-bottom: 1px solid $bg-grey;
  margin-bottom: 22px;

  @media (min-width: $tablet-width) {
    padding: 0 0 5px 25px;
    margin: 0;
    border: none;
    column-gap: 24px;
  }
}

.footer__image {
  margin: 0 auto 33px;

  &:hover {
    opacity: 0.8;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}

.footer__developers {
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-height: 34px;

  &:hover {
    .footer__developers-img {
      fill: $green;
    }
  }

  &:active {
    opacity: 0.3;
  }

  @media (min-width: $tablet-width) {
    column-gap: 22px;
  }
}

.footer__icon-img {
  fill: $icon-color;

  &:hover {
    fill: $green;
  }

  &:active {
    opacity: 0.6;
  }

  @media (min-width: $tablet-width) {
    &--vkontakte {
      width: 30px;
      height: 22px;
    }

    &--inst {
      width: 23px;
      height: 23px;
    }

    &--facebook {
      width: 28px;
      height: 27px;
    }
  }
}

.footer__html-academy-logo {
  margin-left: 4px;

  @media (min-width: $tablet-width) {
    margin: 0;
  }
}

.footer__developers-logo {
  margin: 0;
}

.footer__developers-img {
  fill: $grey;
}
