}

.no-js .burger {
  display: none;
}
.no-js .nav__wrapper {
  display: block;
}

.burger {
  border: none;
  position: absolute;
  display: block;
  width: 30px;
  height: 30px;
  background-color: transparent;
  cursor: pointer;
  right: -3px;
  top: -34px;
}
.burger::after {
  position: absolute;
  content: "";
  background-image: url("../img/icons/burger-closed.svg");
  background-repeat: no-repeat;
  background-position: center;
  width: 24px;
  height: 16px;
  z-index: 2;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}
.burger--opened::before, .burger--opened::after {
  content: "";
  position: absolute;
  top: 50%;
  left: 10%;
  transform: translate(-50%, -50%);
  width: 25px;
  height: 2px;
  background-color: #000000;
}
.burger--opened::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.burger--opened::after {
  transform: rotate(-45deg);
}
@media (min-width: 768px) {
  .burger {
    display: none;
  }
}

